.blog {
  text-align: center;
  color: $medium_black;

  & > h2 {
    @include text(34px, 44px, 700);
    letter-spacing: 0.03em;
    margin-bottom: 18px;
  }

  & > a {
    @include text(16px, 21px, 500);
    border-bottom: 1px solid $basic_color;
    text-decoration: none;
    color: $medium_black;
    cursor: pointer;
  }
}

.blog__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "care trend"
    "power trend";
  grid-gap: 30px;
  @include padding(62px, 130px);

  @media (max-width: $desktop-width) {
    display: flex;
    flex-direction: column;
  }
}

.blog__item {
  cursor: pointer;
  margin-bottom: 30px;
  background-color: $light_grey;
  grid-area: care;
  margin: 0;

  @media (max-width: $desktop-width) {
    margin-bottom: 50px;
  }

  &:nth-child(3n+2) {
    grid-area: power;
  }

  &:nth-child(3n+3) {
    grid-area: trend;
  }

  &:hover,
  &:focus {
    opacity: 0.5;
  }
}

.blog__link {
  text-decoration: none;
  display: flex;
  flex-direction: row-reverse;

  @media (max-width: $tablet-width) {
    flex-direction: column-reverse;
  }

  & img {
    height: 100%;

    @media (max-width: $desktop-width) {
      min-width: 100%;
    }

    @media (max-width: $tablet-width) {
      width: 100%;
    }
  }

  .blog__item:nth-child(3n+2) & {
    flex-direction: row;

    @media (max-width: $tablet-width) {
      flex-direction: column-reverse;
    }
  }

  .blog__item:nth-child(3n+3) & {
    position: relative;

    @media (max-width: $desktop-width) {
      display: flex;
      flex-direction: row-reverse;
    }

    @media (max-width: $tablet-width) {
      flex-direction: column-reverse;
    }
  }
}

.blog__text {
  min-width: 50%;
  background-color: $light_grey;
  @include padding(70px, 70px);

  @media (max-width: $desktop-width) {
    @include padding(88px, 88px);
  }

  @media (max-width: $tablet-width) {
    @include padding(49px, 49px);
  }

  & > time {
    @include text(14px, 18px, 500);
    color: $basic_grey;
  }

  & > p {
    @include text(26px, 34px, 600);
    color: $basic_black;
    font-family: $caption-font;
  }

  & > span {
    @include text(16px, 21px, 500);
    text-decoration: none;
    color: $medium_black;
    font-family: $caption-font;
    border-bottom: 1px solid $basic_color;
  }

  .blog__item:nth-child(3n+3) & {
    position: absolute;
    bottom: 0;

    @media (max-width: $desktop-width) {
      position: relative;
    }
  }
}
